@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-width: thin;
  /* Firefox */
  font-family: "Montserrat", sans-serif;
}

*::-webkit-scrollbar {
  width: 8px;
  background-color: grey;
}

.App {
  /* background: linear-gradient( 217deg, #a2f2ff, rgba(255, 0, 0, 0) 70.71%), linear-gradient( 127deg, rgb(255 223 254 / 80%), rgba(0, 255, 0, 0) 70.71%), linear-gradient( 336deg, rgb(125 78 125 / 80%), rgba(0, 0, 255, 0) 70.71%);
  ;*/
  background-color: #f5fbe0;
  min-height: 98.5vh;
  border: 8px solid rgb(235, 229, 229);
  margin: 5px;
  padding: 5px;
}

a {
  text-decoration: inherit;
  color: inherit;
}

iframe {
  border: none;
  height: 57vh;
}

html {
  background-color: #f5fbe0;
}
