@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-width: thin;
  /* Firefox */
  font-family: "Montserrat", sans-serif;
}

*::-webkit-scrollbar {
  width: 8px;
  background-color: grey;
}

.App {
  /* background: linear-gradient( 217deg, #a2f2ff, rgba(255, 0, 0, 0) 70.71%), linear-gradient( 127deg, rgb(255 223 254 / 80%), rgba(0, 255, 0, 0) 70.71%), linear-gradient( 336deg, rgb(125 78 125 / 80%), rgba(0, 0, 255, 0) 70.71%);
  ;*/
  background-color: #f5fbe0;
  min-height: 98.5vh;
  border: 8px solid rgb(235, 229, 229);
  margin: 5px;
  padding: 5px;
}

a {
  text-decoration: inherit;
  color: inherit;
}

iframe {
  border: none;
  height: 57vh;
}

html {
  background-color: #f5fbe0;
}

.main-title {
    text-transform: uppercase;
    font-size: 3vw;
    font-family: "Montserrat", sans-serif;
    cursor: pointer;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    font-size: 25px;
    border: 1px solid black;
    box-shadow: 4px 4px 2px black;
    padding: 5px 10px;
    margin: auto;
    width: 50%;
    margin-top: 3%;
    margin-bottom: 3%;
}
/*
hr {
    margin: 10px;
    width: 100%;
    background-color: grey;
}*/
span {
    font-size: 30px;
}

.content {
    display: flex;
    justify-content: space-around;
}

.settings {
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 300;
}

img {
    width: 55%;
    align-self: center;
    padding: 8px;
}

@media (max-width: 800px) {
    .content {
        flex-direction: column-reverse;
        align-items: center;
    }
    img {
        width: 85%;
        padding: 0;
        padding-top: 20px;
    }
    .settings {
        width: 100%;
    }
}

.settings-select {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    flex: 0.8 1;
    justify-content: space-evenly;
}
.quiz {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Poppins", sans-serif;
}

.subtitle {
  font-size: 25px;
  border: 1px solid black;
  box-shadow: 4px 4px 2px black;
  padding: 5px 10px;
}

.quizInfo {
  width: 90%;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  margin: 10px;
}

@media (max-width: 614px) {
  .subtitle {
    margin-top: 3%;
  }
  .quizInfo {
    margin-top: 8%;
  }

  .quizInfo span {
    font-size: 11px;
    font-weight: 800;
  }
}

.question {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.singleQuestion {
  width: 95%;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: 5px solid grey;
  padding: 20px;
  margin-top: 10px;
  border: 1px solid black;
  box-shadow: 4px 4px 2px black;
  padding: 5px 10px;
}

.options {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex: 1 1;
  align-items: center;
  justify-content: space-around;
  margin: 10px;
}

.singleOption {
  width: 46%;
  height: 50px;
  padding: 15px 20px;
  margin: 10px;
}

@media (max-width: 614px) {
  .singleOption {
    width: 100%;
  }
}

.controls {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin: 1% 0%;
}

.select {
  background-color: rgb(140, 202, 138);
  color: white;
  box-shadow: 0 0 1px black;
}

.wrong {
  border: 2px solid #ec4444;
  box-shadow: 0 0 1px black;
  color: black;
}

.question {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin-bottom: 1%;
}

button {
  border: none;
  color: black;
  font-size: 1.4rem;
}

.progressBar {
  align-items: center;
  width: 90%;
}

.result {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60vh;
    text-align: center;
}
