.question {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.singleQuestion {
  width: 95%;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: 5px solid grey;
  padding: 20px;
  margin-top: 10px;
  border: 1px solid black;
  box-shadow: 4px 4px 2px black;
  padding: 5px 10px;
}

.options {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  align-items: center;
  justify-content: space-around;
  margin: 10px;
}

.singleOption {
  width: 46%;
  height: 50px;
  padding: 15px 20px;
  margin: 10px;
}

@media (max-width: 614px) {
  .singleOption {
    width: 100%;
  }
}

.controls {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin: 1% 0%;
}

.select {
  background-color: rgb(140, 202, 138);
  color: white;
  box-shadow: 0 0 1px black;
}

.wrong {
  border: 2px solid #ec4444;
  box-shadow: 0 0 1px black;
  color: black;
}

.question {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin-bottom: 1%;
}

button {
  border: none;
  color: black;
  font-size: 1.4rem;
}

.progressBar {
  align-items: center;
  width: 90%;
}
