.quiz {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Poppins", sans-serif;
}

.subtitle {
  font-size: 25px;
  border: 1px solid black;
  box-shadow: 4px 4px 2px black;
  padding: 5px 10px;
}

.quizInfo {
  width: 90%;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  margin: 10px;
}

@media (max-width: 614px) {
  .subtitle {
    margin-top: 3%;
  }
  .quizInfo {
    margin-top: 8%;
  }

  .quizInfo span {
    font-size: 11px;
    font-weight: 800;
  }
}
