.main-title {
    text-transform: uppercase;
    font-size: 3vw;
    font-family: "Montserrat", sans-serif;
    cursor: pointer;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    font-size: 25px;
    border: 1px solid black;
    box-shadow: 4px 4px 2px black;
    padding: 5px 10px;
    margin: auto;
    width: 50%;
    margin-top: 3%;
    margin-bottom: 3%;
}
